* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.footer {
  width: 100%;
  padding: 30px 40px;
  background: #f6f6f6;
}

.footer .container {
  display: flex;
  max-width: 100%;
}

.footer .container .left-box {
  width: 30%;
  padding: 20px 20px;
  border-right: 2px solid #b8b7b7;
}

.footer .container .left-box .box {
  display: flex;
  padding: 10px 20px;
  margin-top: 15px;
}

.footer .container .left-box .box .icon_box {
  font-size: 26px;
  color: #cd1e76;
}

.footer .container .left-box .box .detail {
  margin-left: 10px;
}

.footer .container .left-box .box .detail h3 {
  text-transform: uppercase;
  font-size: 16px;
  color: #cd1e76;
  font-weight: 600;
  letter-spacing: 1px;
}

.footer .container .left-box .box .detail p {
  font-size: 14px;
  color: #232323;
}
